@import '../../scss/variables';
@import '../../scss/functions';
@import '../../scss/mixins';

.flex_wrapper {
  display: flex;
  align-items: center;

  .counter {
    padding: 0 10px;
  }
}

.plus_btn,
.minus_btn {
  position: relative;
  // border: none;
  // cursor: pointer;
  // background-color: $black-3;
  // border-radius: 50%;
  // padding: 20px;
  // transition: all 0.1s;
  @include button($padding: 20px, $font-size: 0, $background-color: $black-3);
  &::before,
  &::after {
    content: '';
    background-color: $white-1;
    height: 5px;
    width: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 10px;
    transform: translate(-50%, -50%);
    transition: all 0.1s;
  }

  &:hover {
    background-color: $yellow;
  }

  &:disabled {
    cursor: default;
    background-color: #2f2f2f;
    box-shadow: none;
    &::before,
    &::after {
      background-color: #5b5b5b;
    }
  }
}

.plus_btn {
  &::after {
    height: 20px;
    width: 5px;
  }
}

.counter {
  font-weight: 600;
  font-size: rem(32);
  color: $white-1;
  display: flex;
  justify-content: center;
  padding: 10px 0;
}
